
import { AppBar, IconButton, Typography, TextField, InputAdornment, Button, Box, Icon, Hidden } from "@material-ui/core";
import SearchIcon from '@material-ui/icons/Search';
import MenuIcon from '@material-ui/icons/Menu';
import $ from 'jquery';
import brandImg from "../images/brandImg.svg";


const Search = ({ handleOpen, size, only }) => (
    <Hidden class="contents" mediaQuery only={only}>
        <TextField margin='dense' size='small' type='search' placeholder='Enter the Name of a City'
            InputProps={{
                startAdornment: (
                    <InputAdornment position="start">
                        <SearchIcon />
                    </InputAdornment>
                ),
            }}
            className={`${size} bg-white text-black rounded-xl`} />

        <Box className='flex space-x-2 '>
            <Button size='small' variant="outlined" className='h-10 lg:self-center'>Search</Button>
            <Button onClick={handleOpen} size='small' variant='outlined' color='error' className='h-10 lg:self-center'>Options</Button>
        </Box>
    </Hidden>
)


function NavBar({ handleOpen }) {
    const handleMenu = () => {
        if ($('#menu').hasClass('hidden')) {
            $('#menu').removeClass('hidden')
            $('#menu').addClass('flex')
        }

        else {
            $('#menu').addClass('hidden')
            $('#menu').removeClass('flex')
        }
    }
    return (
        <AppBar class='bg-black text-white p-4'>
            <Box id='nav-row' class='flex'>
                <Box class='flex space-x-1 items-center'>
                    <Icon fontSize='large'><img alt='brand visual' src={brandImg} /></Icon>
                    <Typography variant='h6'>API Weather App</Typography>
                </Box>
                <Box class='flex justify-center items-center flex-grow space-x-2'>
                    <Search handleOpen={handleOpen} size='w-5/12' only={['xs', 'sm']} />
                </Box>
            </Box>
            <Box class='flex justify-center items-center flex-grow space-x-2'>
                <Search handleOpen={handleOpen} size='w-7/12' only={['xs', 'lg', 'md', 'xl']} />
            </Box>
            <Box class='flex flex-col-reverse justify-center items-center flex-grow space-y-2'>
                <Search handleOpen={handleOpen} size='w-11/12' only={['sm', 'lg', 'md', 'xl']} />
            </Box>
        </AppBar>

        // <AppBar position='static'>
        //     <Box className='flex  w-full'>
        //     <div className=' flex flex-col lg:flex-row  p-2 lg:items-center bg-opacity-900 text-whit flex-grow'>
        //         <div className='flex items-center'>
        //         <div className='hidden lg:block'><IconButton color='inherit' onClick={handleMenu}><MenuIcon /></IconButton></div>
        //             <Typography variant='h6'>API Weather App</Typography>
        //         </div>
        //         <div id='menu' className='hidden  lg:flex bg-red-90 items-center sm:justify-center flex-col-reverse sm:flex-row flex-grow sm:space-x-2 space-y-2
        //         lg:space-y-0'>
        //             <TextField margin='dense' size='small' type='search' placeholder='Enter the Name of a City'
        //                 InputProps={{
        //                     startAdornment: (
        //                       <InputAdornment position="start">
        //                         <SearchIcon />
        //                       </InputAdornment>
        //                     ),
        //                   }}
        //                 className='w-11/12 sm:w-7/12 lg:w-6/12 xl:w-5/12 bg-white text-black rounded-xl' />
        //             <div className='flex space-x-2 '>
        //                 <Button size='small' variant="outlined" className='h-10 lg:self-center'>Search</Button>
        //                 <Button onClick={handleOpen} size='small' variant='outlined' color='error' className='h-10 lg:self-center'>Options</Button>
        //             </div>
        //         </div>


        //         </div>
        //         <div className='lg:hidden text-white self-start ml-auto mt-1'><IconButton color='inherit' onClick={handleMenu}><MenuIcon /></IconButton></div>
        //         </Box>
        // </AppBar>
    )
};


export default NavBar;